import React from 'react'
import Link from 'gatsby-link'
import Helmet from "react-helmet";
import PostListItem from '../../../components/PostListItem'

const Python = ( { data } ) => (
  <div className="container card">
  <Helmet title={ 'General Python - ' + data.site.siteMetadata.title } />
  <div className="row">
      <div className="col-12">
        <h1 className="center">Showing contents of: General Python</h1>
        <hr className="separator"/>
      </div>
    </div>
    <div className="row">
      <div className="col-12">
        <p>Python is one of the most popular programming languages to date, and there is no doubt that its popularity comes from being easy to use.</p>
        <p>Here you will find anything related to general programming using Python, for other topics choose other category in this blog.</p>
      </div>
    </div>
    <div className="row">
      <div className="col-12">

<ol className="post-list">
      {data.allMarkdownRemark.edges.map(({ node }) =>
        <PostListItem 
        id={node.id}
        path={"/journal/"+ node.frontmatter.category +node.frontmatter.path}
        title={node.frontmatter.title}
        date={node.frontmatter.date}
        foreword={node.frontmatter.foreword}
      />
      )}
</ol>
      </div>
    </div>
  </div>
)

export default Python

export const query = graphql
`query PythonQuery {
  site {
    siteMetadata {
      title
    }
  }
  allMarkdownRemark(sort: {order: DESC, fields: [frontmatter___date]} 
    filter: {frontmatter: { category: { eq: "python" } }} ) {
    edges {
      node {
        id
        timeToRead
        frontmatter {
          title
          date(formatString: "MMMM DD, YYYY")
          path
          category
          foreword
        }
        excerpt
      }
    }
  }
}`


